@import"fonts.scss";
@import "modules/variables.scss";
@import "modules/nullstyle.scss";
@import"modules/mixin.scss";
// Подключение файлов стилей страниц и элементов
@import "modules/ui.scss";
//@import "modules/header.scss";
@import "modules/footer.scss";

body {
    color: $first_color;
    background-color: $second_color;
    font-family: $fontfamily;
    font-size: 30px;
    font-style: normal;
    font-weight: 100;

}

// Оболочка
.wrapper {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    overflow: hidden;

}

// Ограничивающий контейнер
._container {
    margin: 0 auto;
    height: 100%;
    max-width: calc($container-width + $container-padding*2 );
    padding: 0 $container-padding;
    // border: 1px solid yellow;
}

.page {

    width: 100%;
    height: 100%;

    &__wrapper {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 100%;

    }

    &__screen {
        flex: 1 0 100%;
        position: relative;

    }

    &__pagination {
        position: absolute;
        top: 50%;
        right: 50px;
        transform: translate(0px, -50%);
        display: flex;
        flex-direction: column;
    }

    &__bullet {
        width: 15px;
        height: 15px;
        background-color: rgba(255, 255, 255, 0.5);
        margin: 10px 0;
        border-radius: 50%;
        transition: background-color 0.3s ease 0s;

        &_active {
            background-color: #fff;
        }
    }
}


.screen {

    &__content {
        position: relative;
        z-index: 2;
        text-align: center;
    }

    &__title {
        padding-top: 20px;
        display: inline-block;
    }
}

.scroll-indicator-next {

    position: absolute;
    top: 60%;
    left: 50px;
    opacity: 1;
    pointer-events: all;
    transition: all 1.5s ease 0s;
    cursor: pointer;

    &:hover {
        .cls-3 {
            transform: rotate(180deg);
        }
    }

    &.swiper-button-disabled {
        opacity: 0;
        pointer-events: none;
        // display: none;
    }
}



.scroll-indicator-prew {

    position: absolute;
    top: 40%;
    left: 50px;
    transform: rotateY(180deg) rotate(180deg) translateY(100%);
    opacity: 1;
    pointer-events: all;
    //display: block;
    cursor: pointer;
    transition: all 1.5s ease 0s;



    &.swiper-button-disabled {
        opacity: 0;
        pointer-events: none;
        // display: none;
    }

    &:hover {
        .cls-3 {
            transform: rotate(180deg);
        }
    }


}



.cls-3 {
    transition: all 1.5s ease 0s;

    &.active {
        transform: rotate(360deg);
    }
}