@charset "UTF-8";
@font-face {
  font-family: "Raleway";
  font-display: swap;
  src: url("../fonts/Raleway-Light.woff2") format("woff2"), url("../fonts/Raleway-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: "Raleway";
  font-display: swap;
  src: url("../fonts/Raleway-Thin.woff2") format("woff2"), url("../fonts/Raleway-Thin.woff") format("woff");
  font-weight: 100;
  font-style: normal;
}
/*Обнуление*/
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: "Raleway";
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

.ibg {
  position: relative;
}

.ibg img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
}

body {
  color: rgb(208, 105, 105);
  background-color: black;
  font-family: "Raleway";
  font-size: 30px;
  font-style: normal;
  font-weight: 100;
}

.wrapper {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
}

._container {
  margin: 0 auto;
  height: 100%;
  max-width: 1130px;
  padding: 0 15px;
}

.page {
  width: 100%;
  height: 100%;
}
.page__wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}
.page__screen {
  flex: 1 0 100%;
  position: relative;
}
.page__pagination {
  position: absolute;
  top: 50%;
  right: 50px;
  transform: translate(0px, -50%);
  display: flex;
  flex-direction: column;
}
.page__bullet {
  width: 15px;
  height: 15px;
  background-color: rgba(255, 255, 255, 0.5);
  margin: 10px 0;
  border-radius: 50%;
  transition: background-color 0.3s ease 0s;
}
.page__bullet_active {
  background-color: #fff;
}

.screen__content {
  position: relative;
  z-index: 2;
  text-align: center;
}
.screen__title {
  padding-top: 20px;
  display: inline-block;
}

.scroll-indicator-next {
  position: absolute;
  top: 60%;
  left: 50px;
  opacity: 1;
  pointer-events: all;
  transition: all 1.5s ease 0s;
  cursor: pointer;
}
.scroll-indicator-next:hover .cls-3 {
  transform: rotate(180deg);
}
.scroll-indicator-next.swiper-button-disabled {
  opacity: 0;
  pointer-events: none;
}

.scroll-indicator-prew {
  position: absolute;
  top: 40%;
  left: 50px;
  transform: rotateY(180deg) rotate(180deg) translateY(100%);
  opacity: 1;
  pointer-events: all;
  cursor: pointer;
  transition: all 1.5s ease 0s;
}
.scroll-indicator-prew.swiper-button-disabled {
  opacity: 0;
  pointer-events: none;
}
.scroll-indicator-prew:hover .cls-3 {
  transform: rotate(180deg);
}

.cls-3 {
  transition: all 1.5s ease 0s;
}
.cls-3.active {
  transform: rotate(360deg);
}